
import { motion } from 'framer-motion'

const Modal = ({modal, setModal, unselect}) => {
  return(
      <div className="modal" onClick={unselect} >
        <motion.img src={modal} alt="grampro" 
        initial={{opacity: 0, y: '-100vh'}} 
        animate = {{opacity: 1, y: '0vh'}} 
        
        />
      </div>
    )
}

export default Modal