import { projectStorage, projectStore, timeStamp } from '../firebase/Config';
import { useState, useEffect } from 'react';



const useStorage = (file) => {
  
  const [progress, setProgress] = useState(0);
  const [error, setError] = useState(0);
  const [url, setUrl] = useState("");
  
  useEffect(() => {
    
    //Refs 
    const storageRef = projectStorage.ref(file.name);
    const storeRef = projectStore.collection('images');
  
    storageRef.put(file).on('state_changed', (snap)=>{
      let parcentage = (snap.bytesTransferred / snap.totalBytes) * 100;
      setProgress(parcentage)
    }, (err) => {
      setError(err) 
    }, 
    async () => {
      const url = await storageRef.getDownloadURL()
      setUrl(url)
      const uploadTime = timeStamp() 
      storeRef.add({url, uploadTime})
    }) 
    
  }, [file])
  
  
  
  
  return{ progress, url, error } 
}

export default useStorage