import UseFirestore  from '../hooks/UseFirestore' 
import { motion } from 'framer-motion'


const ImageGrid = ({setModal}) => {
  
  const { docs } = UseFirestore("images")
  
  return(
      <div className="image-grid">
        {docs.map((doc) => (
         <motion.div className="image-wrap" key={doc.id} onClick={() => setModal(doc.url)}
         layout >
            <motion.img src={doc.url} alt="gramPro" 
            initial ={{opacity: 0}} 
            animate = {{opacity: 1}} 
            transition = {{delay: 1}}
            />
          </motion.div>
        ))} 
      </div>
     )
}

export default ImageGrid