import { useState } from 'react'
import Progress from '../components/Progress'
import Success from '../components/Success' 

const Upload = () => {
     
 const [file, setFile] = useState(null);
 const [error, setError] = useState(null);
 const [success, setSuccess] = useState(false);
 const supportedFiles = ['image/png', 'image/jpeg'];
 
 const changeHandler = (e) => {
     let selected = e.target.files[0]; 
     if(selected && supportedFiles.includes(selected.type)) {
       setFile(selected);
       setError("");
     } else{
       setError('Please select a valid image file (png/jpg)');
       setFile(null)
     }
 } 
      
  return(
      
      <form>
        <div className="add">
        <label>
          <input type="file" onChange={changeHandler} />
          <span>+</span>
        </label>
        </div>
        { file && <div className="file"> {file.name} </div> } 
        { error && <div className="error"> {error} </div> } 
        { file && <Progress success={success} setSuccess={setSuccess} file={file} setFile={setFile} /> } 
        {success && <Success /> } 
      </form>
    
    )
}

export default Upload